import axios from 'axios';
import { isLoggedIn } from '../auth';

/**
* Gets an array of online classes
* @returns {Promise} response - the response of classes
*/
export function getClasses(limit, offset) {
	if (isLoggedIn) {
		return axios.get(`/v1/classes?limit=${limit}&offset=${offset}`);
	}
	return Promise.resolve(new Response({
		message: 'Unauthorised'
	}, {
		status: 403,
		statusText: 'Unauthorised'
	}));
}

/**
* Gets an array of online classes
* @returns {Promise} response - the response of classes
*/
export function getLCMClasses(id, limit, offset) {
	if (isLoggedIn) {
		return axios.get(`/v1/users/${id}/assignclasses?limit=${limit}&offset=${offset}`);
	}
	return Promise.resolve(new Response({
		message: 'Unauthorised'
	}, {
		status: 403,
		statusText: 'Unauthorised'
	}));
}

/**
* Fetches a single class
* @param {String} id - class Id
* @returns {Promise} response - a promise of a single class
*/
export function getClass(id) {
	return axios.get(`/v1/classes/${id}`);
}

/**
* Creates a new online class
* @param {Object} classData - class object
* @returns {Promise} response - a promise of a class to be created
*/
export function createClass(classData) {
	return axios.post('/v1/classes', classData);
}

