/**
 * Edits a course
 * @param {Object} data - Object of the user information.
 * @returns {Promise} avatar - a promise of course to be resolved.
 */
export function uploadAvatar(id, data) {
	if (isLoggedIn()) {
		return axios.patch(`/v1/classes/${id}/avatar`, { file: data });
	}
	return Promise.resolve(new Response(
		{ message: 'Unauthorised' },
		{ status: 403, statusText: 'Unauthorised' }
	));
}
