<template>
	<div>
		<template v-if="loading">
			<div class="loading">
				<Loader />
			</div>
		</template>
		<template v-else>
			<section class="main-grid__row user-profile">
				<div class="user-profile__card">
					<div class="user-profile__primary">
						<p class="user-profile__name">{{ itemData.title }}</p>
					</div>
					<div class="user-profile__section">
						<h3 class="user-profile__heading">Additional Content Information</h3>

						<div class="user-profile__item">
							<p class="user-profile__section__title">Content:</p>
							<p v-html="itemData.content"></p>
						</div>
					</div>
				</div>

				<div class="user-profile__tabs">
					<ul class="nav nav-tabs" id="myTab" role="tablist">
						<li class="nav-item">
							<a class="nav-link active" id="edit-class-tab" data-toggle="tab" href="#edit-class" role="tab" aria-controls="edit-class" aria-selected="false">Edit Additional Content</a>
						</li>
					</ul>
					<div class="tab-content" id="myTabContent">

						<div class="tab-pane fade show active user-profile__tabs__section" id="edit-class" role="tabpanel" aria-labelledby="edit-class-tab">
							<div class="user-profile__notification">
								<div class="user-profile__notification__item">
									<div>
										<form
											method="post"
											@submit.prevent="updateAnAdditionalContent">
											<div class="settings__field">
												<div class="form-row">
													<div class="form-group">
														<div class="settings__value">
															<label for="jobTitle">Title</label>
															<input
																:class="[{'is-invalid': errors.title}]"
																@keydown="clearErrors($event.target.name)"
																v-model="itemData.title"
																name="title"
																value=""
																type="text"
																class="form-control"
																id="jobTitle">
														</div>
														<div class="invalid-feedback">
															{{ errors.title }}
														</div>
													</div>
												</div>

												<div class="form-row">
													<div class="form-group">
														<div class="settings__value">
														<label
															class=""
															for="class-desc">Content</label>
															<editor :init="init" ref="myTextEditor"
																v-model="itemData.content"
																:options="editorOption">
															</editor>
													</div>
														<div class="invalid-feedback">
															{{ errors.content }}
														</div>
													</div>
												</div>
											</div>
											<div class="settings__primary">
												<button
													class="btn btn__save"
													type="button"
													name="button"
													@click.prevent="updateAnAdditionalContent"
													v-bind="{'disabled': loading}">
													Save
												</button>
												<Loader  v-if="loading"/>
											</div>
										</form>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
			</section>
		</template>
	</div>
</template>

<script>
import 'quill/dist/quill.core.css';
import 'quill/dist/quill.snow.css';
import 'quill/dist/quill.bubble.css';
import { quillEditor } from 'vue-quill-editor';
import Loader from '../../components/Loader';
import form from '../../mixins/form';
import { success, fail } from '../../utils/toast';
import classes from '../../utils/validations/classes';
import { getAnAdditionalContent, updateAnAdditionalContent } from '../../utils/api/classes';
import init from '../../config/tinymce';

export default {
	name: 'AdditionalContentDetail',
	mixins: [form],
	data() {
		return {
			init,
			loading: true,
			itemData: {},
			errors: {},
			editorOption: {
				modules: {
					toolbar: [
						['bold', 'italic', 'underline', 'strike'],
						['blockquote', 'code-block'],
						[{ 'header': 1 }, { 'header': 2 }],
						[{ 'list': 'ordered' }, { 'list': 'bullet' }],
						[{ 'script': 'sub' }, { 'script': 'super' }],
						[{ 'indent': '-1' }, { 'indent': '+1' }],
						[{ 'direction': 'rtl' }],
						[{ 'size': ['small', false, 'large', 'huge'] }],
						[{ 'header': [1, 2, 3, 4, 5, 6, false] }],
						[{ 'font': [] }],
						[{ 'color': [] }, { 'background': [] }],
						[{ 'align': [] }],
						['clean'],
						['link', 'image', 'video']
					]
				}
			}
		};
	},
	components: {
		Loader,
		quillEditor
	},
	created() {
		this.getAnAdditionalContent();
	},
	methods: {
		getAnAdditionalContent() {
			const { id, moduleId } = this.$route.params;
			getAnAdditionalContent(moduleId, id)
				.then((response) => {
					if (response.status === 200) {
						this.itemData = response.data.data;
						this.loading = false;
					}
				})
				.catch((error) => {
					this.loading = false;
					return error;
				});
			return true;
		},
		updateAnAdditionalContent() {
			const { content, title } = this.itemData;
			const result = { content, title };
			this.validate(result, classes.updateAnAdditionalContent);
			if (Object.keys(this.issues).length) {
				this.errors = this.issues;
				return false;
			}

			const { id, moduleId } = this.$route.params;
			updateAnAdditionalContent(moduleId, id, result)
				.then((response) => {
					if (response.status === 200) {
						this.loading = false;
						success({
							title: 'Success! Additional Content has been updated!'
						});
					}
				})
				.catch((error) => {
					this.loading = false;
					fail({
						title: 'Ooops! Something went wrong. Pls try again.'
					});
					return error;
				});
			return true;
		}
	},
	computed: {
		editor() {
			return this.$refs.myTextEditor.quill;
		}
	}
};
</script>


